import React, { Component } from 'react';
import './home.css';

import WifiIcon from '@material-ui/icons/Wifi';
import SignalCellular3BarIcon from '@material-ui/icons/SignalCellular3Bar';
import Battery90Icon from '@material-ui/icons/Battery90';

import ColorList from './color-list.jsx'
import MainContent from './main-content.jsx'
import InstagramIcon from '@material-ui/icons/Instagram';
// import InfoIcon from '@material-ui/icons/Info';
// import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import GitHubIcon from '@material-ui/icons/GitHub';
import About from './about-pages.jsx'
import Coffe from './img/Ko-fi_Icon_RGB_rounded.png'

import {
    BrowserRouter as Router,
    Switch,
    Route,
} from "react-router-dom";

class Home extends Component {
    constructor(props) {
        super(props);
        this.clock = React.createRef()
    }

    componentDidMount() {
        const Clock = this.clock.current

        function startTime() {
            var today = new Date();
            var h = today.getHours();
            var m = today.getMinutes();
            // var s = today.getSeconds();
            m = checkTime(m);
            // s = checkTime(s);
            Clock.innerHTML =
                h + ":" + m;
            setTimeout(startTime, 500);
        }
        function checkTime(i) {
            if (i < 10) { i = "0" + i };  // add zero in front of numbers < 10
            return i;
        }

        startTime()

    }

    render() {

        return (
            <div className="home__main">
                <div className="header__main">
                    <a href="https://github.com/bocahgabut" target="_blank" rel="noopener noreferrer" className="info" >
                        <GitHubIcon />
                    </a>
                </div>
                {/* <div className="container__detail">
                    <div className="detail">
                        <div className="detail-header">
                            <div className="back__">
                                <ArrowBackIcon />
                            </div>
                            <div className="title">
                                How to use
                            </div>
                        </div>
                        <div className="detail-body">

                        </div>
                    </div>
                </div> */}
                <div className="container__title">
                    <div className="title">
                        ColorDetections.io
                    </div>
                    <div className="sub">
                        Detection <span>color</span> with your camera
                    </div>
                </div>
                <div className="container__phone">
                    <div className="iphone">
                        <div className="header">
                            <div className="frame__header">
                                <div className="speaker"></div>
                                <div className="camera"></div>
                            </div>
                            <div className="header__bars">
                                <div className="header__bars-block">
                                    <span ref={this.clock}>

                                    </span>
                                </div>
                                <div className="header__bars-block">
                                    <SignalCellular3BarIcon />
                                    <WifiIcon />
                                    <Battery90Icon />
                                </div>
                            </div>
                        </div>
                        <Router>
                            <Switch>
                                <Route exact path="/list">
                                    <div className="body body-scroll">
                                        <div>
                                            <ColorList />
                                        </div>
                                    </div>
                                </Route>
                                <Route exact path="/about">
                                    <div className="body body-scroll">
                                        <div>
                                            <About />
                                        </div>
                                    </div>
                                </Route>
                                <Route exact path="/">
                                    <div className="body ">
                                        <div>
                                            <MainContent />
                                        </div>
                                    </div>
                                </Route>
                            </Switch>
                        </Router>
                        <div className="footer">
                            <div className="frame__footer"></div>
                        </div>
                    </div>
                </div>
                <div className="footer__container">
                    <a href="https://www.instagram.com/boch_gabut/" rel="noopener noreferrer" target="_blank" className="ig__">
                        <InstagramIcon className="custom" />
                    </a>
                    <a href="https://ko-fi.com/Blog/Post/Color-Detections-with-your-camera-F1F62NXIT" rel="noopener noreferrer" target="_blank" className="buy__me">
                        <img src={Coffe} alt="Coffe" />
                        <h5>Support me on ko-fi</h5>
                    </a>
                </div>
            </div>
        );
    }
}

export default Home;
