import React from 'react';
import './index.css';

const About = ({ links, social, name, icons }) => {

    return (
        <a href={(links)} target="_blank" rel="noopener noreferrer" className="about__card">
            <div className="icons__social">
                {(icons)}
            </div>
            <div className="container__info">
                <div className="title__social">{(social)}</div>
                <div className="name__social">{(name)}</div>
            </div>
        </a>
    );
}

export default About;
