import React, { useState } from 'react';
import './index.css';

import Accordion from './accordion.jsx'

function List() {
    const getData = JSON.parse(localStorage.getItem("ClientStorage"))
    const [active, setActive] = useState("")

    return (
        <div className="main__list" >
            {(getData === null
                ?
                <div className="no__item">
                    <h4>No data in here</h4>
                </div>
                :
                getData.map((items) => {
                    return (
                        <Accordion getId={items.id} title={items.color} active={active} setActive={setActive} />
                    )
                }))}
        </div>
    );
}

export default List;
