import React from 'react';
import './App.css';
import NavbarTop from './component/navbar top/index.js';
import ContentAbout from './component/about/index.jsx';

import InstagramIcon from '@material-ui/icons/Instagram';
import FreeBreakfastIcon from '@material-ui/icons/FreeBreakfast';
import GitHubIcon from '@material-ui/icons/GitHub';

function AboutPages() {
    return (
        <div className="main__container">
            <NavbarTop icons="home" />
            <ContentAbout links="https://www.instagram.com/boch_gabut/" social="Instagram" name="@boch_gabut" icons={<InstagramIcon />} />
            <ContentAbout links="https://ko-fi.com/boch_gabut" social="ko-fi" name="boch_gabut" icons={<FreeBreakfastIcon />} />
            <ContentAbout links="https://github.com/bocahgabut" social="Github" name="bochgabut" icons={<GitHubIcon />} />
            <div className="copyright__">
                <h5>&#169; Copyright 2020 ColorDetections.io</h5>
                <h5>Bocah_gabut</h5>
            </div>
        </div>
    );
}

export default AboutPages;
