import React, { Component } from 'react';
import './index.css';

import {
    BrowserRouter as Router,
    Link
} from "react-router-dom";

import FlipCameraAndroidIcon from '@material-ui/icons/FlipCameraAndroid';
import CameraIcon from '@material-ui/icons/Camera';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
var uniqid = require('uniqid');


class VideoContent extends Component {
    constructor(props) {
        super(props);
        this.circleIndicator = React.createRef()
        this.videoTag = React.createRef();
        this.myCanvas = React.createRef();
        this.labelTag = React.createRef();
        this.circleTag = React.createRef();
        this.btnClick = React.createRef();
        this.btnCapture = React.createRef();
        this.elementList = React.createRef();
        this.delColor = React.createRef();

        this.state = {
            cameraType: 'back',
            mirrorMode: false,
            notifCount: 0,
            notifDisplay: 'display',
        };
    }
    
    refreshPage() {
        setTimeout(function () {
            window.location.reload();
        }, 20)
    }

    async componentDidMount() {
        // const labelHexa = this.labelTag.current;
        const containerList = this.elementList.current;
        const btnFlip = this.btnClick.current;
        const circleColor = this.circleTag.current;
        const circleIndi = this.circleTag.current;
        const contexCanvas = this.myCanvas.current.getContext('2d')
        const video = this.videoTag.current;
        const CaptureColor = this.btnCapture.current;
        var setColor = null

        navigator.getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia || navigator.oGetUserMedia || navigator.mediaDevices.getSupportedConstraints();

        const access = await navigator.mediaDevices.getUserMedia({ video: true });

        var front = false;
        // jika user memberikan izin
        if (access) {
            navigator.mediaDevices
                .getUserMedia({ video: { facingMode: (front ? "user" : "environment") } })
                .then(stream => this.videoTag.current.srcObject = stream)
                .catch(console.log);
        }

        frameVideo()

        function flipBtn() {
            if (front == false) {
                front = true
                navigator.mediaDevices
                    .getUserMedia({ video: { facingMode: (front ? "user" : "environment") } })
                    .then(stream => video.srcObject = stream)
                    .catch(console.log);
            } else {
                front = false
                navigator.mediaDevices.getUserMedia({ video: { facingMode: (front ? "user" : "environment") } })
                    .then(stream => video.srcObject = stream)
                    .catch(console.log);
            }
        }

        btnFlip.addEventListener('click', function () {
            flipBtn()
        })

        function AddColor() {
            var tbClient = localStorage.getItem("ClientStorage")
            let colorList

            // var colorList = ({
            //     id: uniqid(),
            //     color: setColor
            // })

            if (tbClient === null) {
                colorList = []
                // colorList = JSON.parse(localStorage.getItem('ClientStorage'))
                // localStorage.setItem('ClientStorage', JSON.stringify(colorList))
            } else {
                // localStorage.setItem('ClientStorage', localStorage.getItem('ClientStorage') + ',' + JSON.stringify(colorList))
                colorList = JSON.parse(localStorage.getItem('ClientStorage'))
            }
            var color = colorList.push({
                id: uniqid(),
                color: setColor
            })

            localStorage.setItem('ClientStorage', JSON.stringify(colorList))
            // console.log(localStorage.getItem("ClientStorage"))
            // showColor()
        }

        CaptureColor.addEventListener('click', function () {
            AddColor()
        })

        // function showColor() {
        //     var colorEach = localStorage.getItem("ClientStorage")
        //     var data = JSON.parse(colorEach)
        //     var clicked = `onClick={this.handleClick()}`

        //     containerList.innerHTML = ''
        //     for (var i = 0; i < data.length; i++) {
        //         // console.log(data[i].color)
        //         containerList.innerHTML += `<div class="main__color">
        //         <div style="background: ${data[i].color}" class="cirle__color">
        //             <div class="icons" data-id="${data[i].id}" ${clicked}>
        //                 X
        //             </div>
        //         </div>
        //         <label htmlFor="cirle__color">
        //             ${data[i].color}
        //         </label>
        //         </div>`
        //     }
        //     // console.log(data.length)
        // }

        // if (localStorage.getItem("ClientStorage") === null) {
        //     //nothing
        // } else {
        //     // if (showColor()) {
        //     showColor()
        //     // }
        // }

        function frameVideo() {
            contexCanvas.drawImage(video, 0, 0, video.offsetWidth, video.offsetHeight / 1.25);

            let frame = contexCanvas.getImageData(0, 0, video.offsetWidth, video.offsetHeight / 1.25);

            contexCanvas.putImageData(frame, 0, 0);
            contexCanvas.scale(-1, -1)

            setTimeout(frameVideo, 0)
        }

        const width = Math.floor(video.offsetWidth / 2);
        const height = Math.floor(circleIndi.offsetTop * 8.9);
        console.log(width + " " + height)

        // drawCircle()

        function drawCircle() {
            var ctx = contexCanvas;
            ctx.beginPath();
            ctx.arc(width, height, 2, 0, 2 * Math.PI);
            ctx.stroke();
        }

        drawCircle()

        function imageData() {
            var pixelData = contexCanvas.getImageData(width, height, 1, 1);
            var data = pixelData.data;

            // console.log(data);
            ConvertToHex(data[0], data[1], data[2]);
            setTimeout(() => {
                imageData()
            }, 1000);
        }

        var than =
            `
  xxxxxxxxxxx  xxx       xxx    xxxxxxxxxxxxxx   xxxxxx        xxx   xxx        xxx   
  xxxxxxxxxxx  xxx       xxx    xxxxxxxxxxxxxx   xxxxxx        xxx   xxx       xxx
      xxx      xxx       xxx    xxx        xxx   xxx xxx       xxx   xxx      xxx
      xxx      xxxxxxxxxxxxx    xxx        xxx   xxx  xxx      xxx   xxxxxxxxxxx
      xxx      xxxxxxxxxxxxx    xxxxxxxxxxxxxx   xxx   xxx     xxx   xxxxxxxxxx
      xxx      xxx       xxx    xxxxxxxxxxxxxx   xxx    xxx    xxx   xxx     xxx
      xxx      xxx       xxx    xxx        xxx   xxx     xxxxxxxxx   xxx      xxx
      xxx      xxx       xxx    xxx        xxx   xxx      xxxxxxxx   xxx       xxx
            `
        var k =
            `
      xxx         xxx  xxxxxxxxxxxxxxx   xxx         xxx
       xxx       xxx   xxxxxxxxxxxxxxx   xxx         xxx
        xxx     xxx    xxx         xxx   xxx         xxx
         xxx   xxx     xxx  xxxxx  xxx   xxx         xxx
          xxx xxx      xxx  xxxxx  xxx   xxx         xxx
            xxx        xxx         xxx   xxx         xxx
            xxx        xxxxxxxxxxxxxxx   xxxxxxxxxxxxxxx
            xxx        xxxxxxxxxxxxxxx   xxxxxxxxxxxxxxx
        `
        if (video.play()) {
            setTimeout(() => {
                imageData()
                // console.log(than + k)
                console.clear()
            }, 1000);
        }

        function ConvertToHex(r, g, b) {
            var hexaColor = "#" + getHexa(r) + getHexa(g) + getHexa(b);
            // labelHexa.innerHTML = "" + hexaColor;
            setColor = hexaColor
            circleColor.style.backgroundColor = hexaColor
            // console.log(color)
        }

        function getHexa(value) {
            var hexa = parseInt(value).toString(16);
            return hexa.length == 1 ? "0" + hexa : hexa;
        }

    }

    notif() {
        this.setState({ notifDisplay: 'display', notifCount: this.state.notifCount + 1 })
    }

    componentDidUpdate() {
        var Badges = document.querySelector('.badges');
        var notifFinal = "", notifGet = this.state.notifCount
        if (notifGet > '9') {
            notifFinal = '9+'
        } else {
            notifFinal = this.state.notifCount
        }
        Badges.style = "animation: shake 0.82s cubic-bezier(.36,.07,.19,.97);";
        Badges.innerHTML = notifFinal;
        setTimeout(() => {
            Badges.style = ""
        }, 500);
    }

    render() {


        return (
            <div className="main__content">
                <div div className="video__container" >
                    <video
                        ref={this.videoTag}
                        id="video-tracking"
                        autoPlay
                    />
                    <canvas className="hidden__canvas" ref={this.myCanvas}>
                    </canvas>
                    <div
                        ref={this.circleIndicator}
                        className="circle">
                    </div>
                    <div
                        className="circle-container">
                        <div className="circle__item" style={{ animationDelay: '0s' }}></div>
                        <div className="circle__item" style={{ animationDelay: '1s' }}></div>
                        <div className="circle__item" style={{ animationDelay: '2s' }}></div>
                        <div className="circle__item" style={{ animationDelay: '3s' }}></div>
                    </div>
                    <div className="hover__item marker">
                        <div
                            ref={this.circleTag}
                            className="circle__color">
                        </div>
                        {/* <h5 ref={this.labelTag}></h5> */}
                    </div>
                </div >
                <div className="bottom__content">
                    {/* <div className="color__list" >
                        <div className="container__color" ref={this.elementList}>
                            <div class="main__color">
                                <div class="cirle__color">
                                    <div class="icons" ref={this.delColor}>
                                        X
                                    </div>
                                </div>
                                <label htmlFor="cirle__color">

                                </label>
                            </div>
                        </div>
                    </div> */}
                    <div className="btn__container">
                        <div ref={this.btnClick} className="btn btn__flip">
                            <FlipCameraAndroidIcon id="icons" />
                        </div>
                        <div
                            className="btn__shot"
                            ref={this.btnCapture}
                            onClick={() => this.notif()}
                        >
                            <CameraIcon id="camera" />
                        </div>
                        <Router>
                            <Link onClick={()=> this.refreshPage()} to="/about" className="btn btn__flip">
                                <MoreHorizIcon id="icons" />
                            </Link>
                        </Router>
                    </div>
                </div>
            </div>
        )
    }

}

export default VideoContent;
