import React from 'react';

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';

var convert = require('color-convert');
const Accordion = ({ getId, title, active, setActive }) => {

    const HSL = convert.hex.hsl(title)
    const HSV = convert.hex.hsv(title)
    const RGB = convert.hex.rgb(title)
    const CMYK = convert.hex.cmyk(title)

    const handleDelete = (id) => {
        // alert(id)
        let setId = `["${id}"]`
        const getData = JSON.parse(localStorage.getItem("ClientStorage"))
        var i = getData.length
        while (i--) {
            if (setId.indexOf(getData[i].id) != -1) {
                getData.splice(i, 1);
            }
        }
        localStorage.setItem("ClientStorage", JSON.stringify(getData))
        // console.log(getData)
        window.location.reload();
    }

    return (
        <div className="card__">
            <div className="card__list">
                <div className="card__heading">
                    <div>
                        <div className="color__heading" style={{ background: title }}></div>
                        <div className="title__heading">{(title)}</div>
                    </div>
                    <div>
                        <div className="card__expands" onClick={() => handleDelete(getId)}>
                            <DeleteForeverIcon />
                        </div>
                        <div className={(active === title ? 'active ' : "") + "card__expands"} onClick={() => setActive(title)} >
                            <ExpandMoreIcon />
                        </div>
                    </div>
                </div>
                <div className={(active === title ? 'expands ' : "") + "card__body"}>
                    <div>
                        <div className="card__body-items">
                            <div className="title">
                                HEXAL
                            </div>
                            <div className="items">
                                {title}
                            </div>
                        </div>
                        <div className="card__body-items">
                            <div className="title">
                                Cyan Magenta Yellow Key (CMYK)
                            </div>
                            <div className="items">
                                {CMYK[0] + ',' + CMYK[1] + ',' + CMYK[2]}
                            </div>
                        </div>
                        <div className="card__body-items">
                            <div className="title">
                                Red Green Blue (RGB)
                            </div>
                            <div className="items">
                                {RGB[0] + ',' + RGB[1] + ',' + RGB[2]}
                            </div>
                        </div>
                        <div className="card__body-items">
                            <div className="title">
                                Hue saturation Lightness (HSL)
                            </div>
                            <div className="items">
                                {HSL[0] + ',' + HSL[1] + '%' + ',' + HSL[2] + '%'}
                            </div>
                        </div>
                        <div className="card__body-items">
                            <div className="title">
                                Hue Saturation Value (HSV)
                            </div>
                            <div className="items">
                                {HSV[0] + ',' + HSV[1] + '%' + ',' + HSV[2] + '%'}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Accordion;